import React, { Component } from 'react';
import { render } from 'react-dom';
import Dropdown_box from '../Dropdown_box';
import './style.css';

class Dropdown_menu extends Component {
  constructor(props) {
    super(props);
    this.handleMouseHover = this.handleMouseHover.bind(this);
    this.state = {
      isHovering: false,
    };
  }

  handleMouseHover() {
    this.setState(this.toggleHoverState);
  }

  toggleHoverState(state) {
    return {
      isHovering: !state.isHovering,
    };
  }

  render() {
    return (
      <div className="our_service__link">



        <div className="our_service_dropdown__item"
          onMouseEnter={this.handleMouseHover}
          onMouseLeave={this.handleMouseHover}
        >


		<h4>наши услуги</h4>


        </div>
        {this.state.isHovering && <div className="my_dropdown__box"> <Dropdown_box /> </div>}
      </div>
    );
  }
}

export default Dropdown_menu;
